*{
    box-sizing: border-box;
    margin: 0;
}

.carousel {
    overflow: hidden;
    position: relative;
}

.inner {
    display: flex;
}

.carousel-item {
    flex: 0 0 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 400px;
    background-color: #333;
    transition: background-color 0.3s, color 0.3s;
    overflow: hidden;
}

.carousel-item img {
    max-width: 100%;
    max-height: 100%;
    display: block;
    margin: auto;
}

.indicators {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
}

.indicators>button {
    margin: 0 5px;
    border: none;
    background-color: transparent;
    cursor: pointer;
    font-size: 16px;
    font-weight: bolder;
    color: #1c34be;
    transition: color 0.3s;
}

.indicators>button.active {
    color: rgb(215, 2, 2);
}

.side-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 40px;
    height: 40px;
    background-color: rgba(0, 0, 0, 0.5);
    color: #fff;
    font-size: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
    z-index: 1;
}

.side-button.left {
    left: 10px;
}

.side-button.right {
    right: 10px;
}